<template>
    <div class="edit">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
            <el-form-item label="" prop="title">
                <el-input v-model="ruleForm.title"></el-input>
            </el-form-item>
            <el-form-item label="" prop="phone">
                <el-input v-model="ruleForm.phone"></el-input>
            </el-form-item>
            <el-form-item label="" prop="Identity">
                <el-select v-model="ruleForm.Identity" placeholder="">
                    <el-option label="" value="passenger"></el-option>
                    <el-option label="" value="driver"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" required>
                <el-col :span="6">
                    <el-form-item prop="date1">
                        <el-date-picker type="date" placeholder="" v-model="ruleForm.date1"
                                        style="width: 100%;"></el-date-picker>
                    </el-form-item>
                </el-col>
                <el-col class="line" :span="3">-</el-col>
                <el-col :span="6">
                    <el-form-item prop="date2">
                        <el-time-picker type="fixed-time" placeholder="" v-model="ruleForm.date2"
                                        style="width: 100%;"></el-time-picker>
                    </el-form-item>
                </el-col>
                <el-col class="line" :span="3">-</el-col>
                <el-col :span="6">
                    <el-form-item prop="date3">
                        <el-time-picker type="fixed-time" placeholder="" v-model="ruleForm.date3"
                                        style="width: 100%;"></el-time-picker>
                    </el-form-item>
                </el-col>
            </el-form-item>
            <el-form-item label="" required>
                <el-col :span="11">
                    <el-form-item prop="start">
                        <el-input placeholder="" v-model="ruleForm.start" style="width: 100%;"></el-input>
                    </el-form-item>
                </el-col>
                <el-col class="line" :span="2">-</el-col>
                <el-col :span="11">
                    <el-form-item prop="end">
                        <el-input placeholder="" v-model="ruleForm.end" style="width: 100%;"></el-input>
                    </el-form-item>
                </el-col>
            </el-form-item>
            <el-form-item label="" prop="desc">
                <el-input type="textarea" v-model="ruleForm.desc"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
                <el-button @click="resetForm('ruleForm')"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    export default {
        name: "Edit",
        data() {
            return {
                blogId: '',
                ruleForm: {
                    title: '',
                    phone: '',
                    Identity: '',
                    date1: '',
                    date2: '',
                    date3: '',
                    start: '',
                    end: '',
                    desc: ''
                },
                rules: {
                    title: [
                        {required: true, message: '', trigger: 'blur'},
                        {min: 2, max: 8, message: ' 2  8 ', trigger: 'blur'}
                    ],
                    phone: [
                        {required: true, message: '', trigger: 'blur'},
                        {min: 8, max: 11, message: '', trigger: 'blur'}
                    ],
                    Identity: [
                        {required: true, message: '', trigger: 'change'}
                    ],
                    date1: [
                        {type: 'date', required: true, message: '', trigger: 'change'}
                    ],
                    date2: [
                        {type: 'date', required: true, message: '', trigger: 'change'}
                    ],
                    date3: [
                        {type: 'date', required: true, message: '', trigger: 'change'}
                    ],
                    start: [
                        {required: true, message: '', trigger: 'blur'},
                    ],
                    end: [
                        {required: true, message: '', trigger: 'blur'},
                    ],
                    desc: [
                        {required: true, message: '', trigger: 'blur'}
                    ]
                },
            };
        },
        created() {
            this.blogId = this.$route.params.blogId;
            this.getBlog();
        },
        methods: {
            getBlog() {
                let query = new this.$AV.Query('Blogs');
                query.get(this.blogId).then(res => {
                    let data = res.toJSON()
                    this.ruleForm.title = data.title
                    this.ruleForm.phone = data.phone
                    this.ruleForm.Identity = data.Identity
                    this.ruleForm.date1 = new Date(data.date1)
                    this.ruleForm.date2 = new Date(data.date2)
                    this.ruleForm.date3 = new Date(data.date3)
                    this.ruleForm.start = data.start
                    this.ruleForm.end = data.end
                    this.ruleForm.desc = data.desc
                });
            },
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        let blog = this.$AV.Object.createWithoutData('Blogs', this.blogId);
                        blog.save({
                            title: this.ruleForm.title,
                            phone: this.ruleForm.phone,
                            Identity: this.ruleForm.Identity,
                            date1: this.ruleForm.date1,
                            date2: this.ruleForm.date2,
                            date3: this.ruleForm.date3,
                            start: this.ruleForm.start,
                            end: this.ruleForm.end,
                            desc: this.ruleForm.desc,
                        }).then(res => {
                            this.$notify({
                                title: '',
                                message: ' ()"',
                                type: 'success',
                                offset: 100
                            });
                            this.$router.push({
                                name: 'Detail',
                                params: {blogId: this.blogId}
                            });
                        });
                    } else {
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            }
        }
    }
</script>

<style lang="scss" scoped>
    .edit {
        width: 650px;
    }

    .line {
        text-align: center;
    }
</style>