<template>
    <div class="my">
        <div class="user">
            <div class="avatar">
                <img :src="user.avatar" alt="">
                <div class="name">{{user.username}} </div>
            </div>
            <ul>
                <li><span></span><span>{{user.profession}}</span></li>
                <li><span></span><span>{{user.grade}}</span></li>
                <li><span></span><span>{{user.gender}}</span></li>
                <li><span></span><span>{{user.phoneNumber}}</span></li>
                <li><span>QQ</span><span>{{user.qq}}</span></li>
                <li><span></span><span>{{user.signature}}</span></li>
            </ul>
            <div class="time"><span></span><span>{{user.createdAt | formatDate}}</span></div>
        </div>
        <div class="decs" v-for="blog in blogList">
            <div class="content">
                <div class="date">
                    <p>{{blog.createdAt | day}}</p>
                    <p>{{blog.createdAt | month}}</p>
                    <p>{{blog.createdAt | year}}</p>
                </div>
                <div>
                    <h2>{{blog.title}}</h2>
                    <div>{{blog.desc}}</div>
                </div>
            </div>
            <div>
                <el-button type="primary" icon="el-icon-edit"
                           @click="$router.push({name:'Edit',params:{blogId:blog.id}})">
                </el-button>
                <el-button type="danger" icon="el-icon-delete" @click="deleteBlog(blog.id)"></el-button>
            </div>
        </div>
        <div class="pag">
            <el-pagination
                    background
                    layout="prev, pager, next"
                    :total="1">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import {mapGetters} from 'vuex';

    export default {
        data() {
            return {
                blogList: [],
                page: 1,
                total: 0,

            }
        },
        computed: {
            ...mapGetters(['user'])
        },
        created() {
            this.page = this.$route.query.page - 0 || 1;
            this.blogList = this.getUserBlog(this.page);
        },
        methods: {
            getUserBlog(page) {
                let query = new this.$AV.Query('Blogs');
                query.descending('createdAt');
                query.equalTo('userId', this.user.objectId);
                query.count().then(count => {
                    this.total = count;
                });
                query.limit(10); //  10 
                query.skip((page - 1) * 10);
                let list = [];
                query.find().then(blogs => {
                    blogs.forEach((blog, index) => {
                        list.push({
                            id: blog.id,
                            title: blog.attributes.title,
                            desc: blog.attributes.desc,
                            createdAt: blog.createdAt,
                            updatedAt: blog.updatedAt,
                        });
                    })
                });
                return list
            },
            changePage(page) {
                this.getUserBlog(page);
            },
            deleteBlog(blogId) {
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    var blog = this.$AV.Object.createWithoutData('Blogs', blogId);
                    blog.destroy().then(success => {
                        this.$notify({
                            title: '',
                            message: ' ()',
                            type: 'success',
                            offset: 100
                        });
                        this.blogList = this.getUserBlog(1);
                    }, err => {
                        this.$notify.error({
                            title: '',
                            message: ' (',
                            offset: 100
                        });
                    });
                })
            },
        },
    }
</script>

